/* Call to Action Button */
.cta-btn {
  display: inline-block;
  position: relative;
  padding: 0.8rem $default-font-size;
  font-weight: bold;
  line-height: 1;
  z-index: 1;
  transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.6s;

  &::after {
    content: '';
    display: block;
    position: absolute;
    width: 0px;
    height: 100%;
    left: 0;
    bottom: 0;
    z-index: -1;
    transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.4s;
  }
}

/* Hero Style */
.cta-btn--hero {
  background-image: linear-gradient(135deg, #163750 0%, #163750 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  border: 5px solid #173751;
  border-image-slice: 1;
  cursor: pointer;
  color: $primary-color;


  &::after {
    background-image: linear-gradient(135deg, #163750 0%, #163750 100%);

  }

  &:hover {
    -webkit-text-fill-color: $white-color;
    text-decoration: none;
    color: $white-color;

    &::after {
      width: 100%;
    }
  }
}